@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  font-size: 10px;
}
body {
  position: relative;
  margin: 0;
  padding: 8rem 0;
  height: 100%;
  width: 100%;
  font-family: "Montserrat", sans-serif;
  background-color: #ebecf0;
}

@media screen and (max-width: 599px) {
  body {
    padding: 2.5rem 0;
  }
}

@media (min-width: 600px) and (max-width: 1024px) {
  body {
    padding: 4rem 0;
  }
}

.EditingForm {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.EditingForm input,
.EditingForm button {
  padding: 1.6rem;
  font-size: 1.6rem;
  border-radius: 2.5rem;
  border: 0;
  outline: 0;
  background-color: #ebecf0;
}
.EditingForm label {
  width: 100%;
}
.EditingForm input {
  width: 97%;
  font-family: "Montserrat", sans-serif;
  color: #666;
  box-shadow: inset 2px 2px 5px #babecc, inset -5px -5px 10px #fff;
  box-sizing: border-box;
  text-shadow: 1px 1px 0 #fff;
  transition: all 0.2s ease-in-out;
}
.EditingForm input:focus {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fff;
}
.EditingForm button {
  width: 38px;
  padding: 1rem;
  font-size: 1.6rem;
  background-color: #ebecf0;
  color: #838aa0;
  border: 0;
  outline: 0;
  border-radius: 1.2rem;
  text-shadow: 1px 1px 0 #fff;
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
  cursor: pointer;
}
.EditingForm button:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}
.EditingForm .edit {
  margin: 0;
}

@media screen and (max-width: 599px) {
  .EditingForm input,
  .EditingForm button {
    padding: 1.2rem;
    font-size: 1.2rem;
  }
}

@media (min-width: 600px) and (max-width: 1024px) {
  .EditingForm input,
  .EditingForm button {
    padding: 1.4rem;
    font-size: 1.4rem;
  }
}

.TodoItems {
  margin-bottom: 2.5rem;
  padding: 1.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.6rem;
  color: #666;
  background-color: #ebecf0;
  border: 0;
  outline: 0;
  border-radius: 2.5rem;
  text-shadow: 1px 1px 0 #fff;
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
}
.TodoItems:last-child {
  margin-bottom: 0;
}
.TodoItems.isEditing {
  padding: 0.95rem 1.6rem;
}
.TodoItems .check.isChecked,
.TodoItems.isEditing .edit {
  box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1),
    inset 3px 3px 5px rgba(0, 0, 0, 0.1);
}
.TodoItems .check.isChecked .fa-check,
.TodoItems.isEditing .edit .fa-edit {
  transform: scale(0.9);
}
.TodoItems .task {
  display: inline-block;
  position: relative;
  word-break: break-all;
}
.TodoItems .task::after {
  content: "";
  position: absolute;
  bottom: 40%;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #666;
  transition: all 0.3s ease;
}
.TodoItems .task.isChecked::after {
  width: 100%;
}
.TodoItems button {
  width: 38px;
  padding: 1rem;
  font-size: 1.6rem;
  color: #838aa0;
  background-color: #ebecf0;
  border: 0;
  outline: 0;
  border-radius: 1.2rem;
  text-shadow: 1px 1px 0 #fff;
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
  cursor: pointer;
}
.TodoItems .left,
.TodoItems .right {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.TodoItems button:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}
.TodoItems .check {
  margin-right: 1rem;
}
.TodoItems .delete {
  margin: 0 1rem;
}
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 500ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 500ms;
}

@media screen and (max-width: 599px) {
  .TodoItems {
    padding: 1.2rem;
    margin-bottom: 1.5rem;
    font-size: 1.4rem;
  }
  .TodoItems button {
    width: 30px;
    padding: 0.8rem;
    font-size: 1.2rem;
  }
}

@media (min-width: 600px) and (max-width: 1024px) {
  .TodoItems {
    padding: 1.4rem;
    margin-bottom: 1.8rem;
  }
  .TodoItems button {
    width: 30px;
    padding: 0.8rem;
    font-size: 1.3rem;
  }
}

.TodoList {
  width: 100%;
  text-align: left;
  list-style: none;
}
.TodoList .fade-enter {
  opacity: 0.01;
  transform: translate(0, 10px);
}
.TodoList .fade-enter-active {
  opacity: 1;
  transform: translate(0, 0);
  transition: all 300ms ease-in;
}
.TodoList .fade-exit {
  opacity: 1;
  transform: translate(0, 0);
}
.TodoList .fade-exit-active {
  opacity: 0.01;
  transform: translate(0, -10px);
  transition: all 300ms ease-out;
}

.Form {
  margin-bottom: 4.5rem;
  display: flex;
  flex-direction: column;
}
.Form input,
.Form button {
  padding: 1.6rem;
  font-size: 1.6rem;
  background-color: #ebecf0;
  border: 0;
  outline: 0;
  border-radius: 2.5rem;
}
.Form label {
  width: 100%;
  margin-bottom: 4rem;
}
.Form input {
  width: 100%;
  color: #666;
  font-family: "Montserrat", sans-serif;
  box-shadow: inset 2px 2px 5px #babecc, inset -5px -5px 10px #fff;
  box-sizing: border-box;
  text-shadow: 1px 1px 0 #fff;
  transition: all 0.2s ease-in-out;
}
.Form input::-webkit-input-placeholder {
  color: #838aa0;
}
.Form input:-ms-input-placeholder {
  color: #838aa0;
}
.Form input::placeholder {
  color: #838aa0;
}
input:focus {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fff;
}
.Form button {
  width: 40%;
  margin: 0 auto;
  color: #61dafb;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}
.Form button:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}
.Form button:active {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fff;
}
.Form span {
  margin-right: 1rem;
}

@media screen and (max-width: 599px) {
  .Form {
    margin-bottom: 2rem;
  }
  .Form input,
  .Form button {
    padding: 1.4rem;
    font-size: 1.4rem;
  }
  .Form label {
    margin-bottom: 2rem;
  }
  .Form input {
    width: 100%;
  }
  .Form button {
    width: 60%;
  }
}

@media (min-width: 600px) and (max-width: 1024px) {
  .Form {
    margin-bottom: 2.5rem;
  }
  .Form input,
  .Form button {
    padding: 1.4rem;
  }
  .Form input {
    width: 100%;
  }
  .Form label {
    margin-bottom: 2.5rem;
  }
}

.TodoApp {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding: 0;
}

@media (min-width: 600px) and (max-width: 1024px) {
  .TodoApp {
    max-width: 100%;
  }
}

.App {
  text-align: center;
}
.App h1 {
  margin: 0 0 4rem;
  color: #61dafb;
  font-size: 4.8rem;
  letter-spacing: 0.1rem;
  line-height: 1.2;
  text-shadow: 2px 3px 0px rgba(200, 200, 200, 1);
}

@media screen and (max-width: 599px) {
  .App {
    width: calc(100% - 2rem);
    margin: 0 auto;
  }
  .App h1 {
    margin: 0 0 1.6rem;
    font-size: 3rem;
  }
}

@media (min-width: 600px) and (max-width: 1024px) {
  .App {
    width: calc(100% - 4rem);
    margin: 0 auto;
  }
  .App h1 {
    margin: 0 0 2rem;
    font-size: 3.6rem;
  }
}

